<template>
    <div class="register_box">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
            <el-form-item prop="password">
                <el-input
                    v-model="ruleForm.password"
                    :placeholder="i18n.$t('rules.password')"
                    class="row"
                    show-password
                ></el-input>
            </el-form-item>
        </el-form>
        <el-button
            class="row color"
            type="success"
            @click="submitForm('ruleForm')"
            :loading="flag"
        >{{i18n.$t('common').upDatePassword}}</el-button>
    </div>
</template>

<script>
import { Api } from "../../../api";
import { useI18n } from "../../../i18nPlugin";
import { ElMessage } from "element-plus";
export default {
    data() {
        return {
            flag: false,
            ruleForm: {
                password: ""
            },
            rules: {
                password: [
                    {
                        required: true,
                        message: this.i18n.$t("rules").password,
                        trigger: "change"
                    },
                    {
                        min: 8,
                        max: 16,
                        message: this.i18n.$t("rules").passwordmessage,
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    },
    mounted() {
    },
    props: ["form"],
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.flag = true;
                    let obj = {
                        ...this.form,
                        password: this.ruleForm.password
                    };
                    Api.reset_password(obj)
                        .then(res => {
                            if (res.status == 200) {
                                ElMessage({
                                    type: "success",
                                    message: this.$t("common.success")
                                });
                                this.$router.push('/mobile/success?password='+this.ruleForm.password+'&email='+this.form.email)
                                
                            }
                            this.flag = false;
                        })
                        .catch(err => {
                            if (err.response.data.code == 400) {
                                ElMessage.error(
                                    this.i18n.$t("common").networkErr
                                );
                            }
                            if (err.response.data.code == 2003) {
                                ElMessage.error(
                                    this.i18n.$t("common").emaillErr
                                );
                            }
                            if (err.response.data.code == 2006) {
                                ElMessage.error(
                                    this.i18n.$t("Businesses").errname
                                );
                            }
                            this.flag = false;
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        }
    }
};
</script>

<style lang=scss scoped>
.register_box {
    width: 300px;
    min-height: 100px;
    padding: 20px;
    background-color: #ffffff;
    margin-top: 70px;
    border: 1px solid #dcdfe6;
    border-radius: 15px;
    margin-left: 50%;
    transform: translateX(-50%);
    .row {
        margin: 10px 0px;
        width: 100%;
    }
}
.el-form-item {
    margin: 22px 0;
}
.color {
    background-color: #68bf99;
    border: 1px solid #1fab89;
}
</style>